import { defineStore } from 'pinia';
import Vue, { computed, ref } from 'vue';
import { loadState } from '@nextcloud/initial-state';
import { callWizard, createConfig, deleteConfig, getConfig } from "../services/ldapConfigService";
export const useLDAPConfigsStore = defineStore('ldap-configs', () => {
  const ldapConfigs = ref(loadState('user_ldap', 'ldapConfigs'));
  const selectedConfigId = ref(Object.keys(ldapConfigs.value)[0]);
  const selectedConfig = computed(() => {
    return function () {
      let postSetHooks = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : {};
      return new Proxy(ldapConfigs.value[selectedConfigId.value], {
        get(target, property) {
          return target[property];
        },
        set(target, property, newValue) {
          target[property] = newValue;
          (async () => {
            updatingConfig.value++;
            await callWizard('save', selectedConfigId.value, {
              cfgkey: property,
              cfgval: newValue
            });
            updatingConfig.value--;
            if (postSetHooks[property] !== undefined) {
              postSetHooks[property](target[property]);
            }
          })();
          return true;
        }
      });
    };
  });
  const updatingConfig = ref(0);
  async function create() {
    const configId = await createConfig();
    const config = await getConfig(configId);
    ldapConfigs.value[configId] = config;
    selectedConfigId.value = configId;
    return configId;
  }
  async function copyConfig(fromConfigId) {
    const configId = await createConfig();
    ldapConfigs.value[configId] = {
      ...ldapConfigs.value[fromConfigId]
    };
    selectedConfigId.value = configId;
    return configId;
  }
  async function removeConfig(configId) {
    const result = await deleteConfig(configId);
    if (result === true) {
      Vue.delete(ldapConfigs.value, configId);
    }
    const firstConfigId = Object.keys(ldapConfigs.value)[0] ?? (await create());
    selectedConfigId.value = firstConfigId;
  }
  return {
    ldapConfigs,
    selectedConfigId,
    selectedConfig,
    updatingConfig,
    create,
    copyConfig,
    removeConfig
  };
});