import path from 'path';
import { DialogSeverity, getDialogBuilder, showError, showSuccess } from '@nextcloud/dialogs';
import axios from '@nextcloud/axios';
import { getAppRootUrl, generateOcsUrl } from '@nextcloud/router';
import { t } from '@nextcloud/l10n';
import logger from "./logger";
const AJAX_ENDPOINT = path.join(getAppRootUrl('user_ldap'), '/ajax');
export async function createConfig() {
  const response = await axios.post(generateOcsUrl('apps/user_ldap/api/v1/config'));
  logger.debug('Created configuration', {
    configId: response.data.ocs.data.configID
  });
  return response.data.ocs.data.configID;
}
export async function getConfig(configId) {
  const response = await axios.get(generateOcsUrl('apps/user_ldap/api/v1/config/{configId}', {
    configId
  }));
  logger.debug('Fetched configuration', {
    configId,
    config: response.data.ocs.data
  });
  return response.data.ocs.data;
}
export async function updateConfig(configId, config) {
  const response = await axios.put(generateOcsUrl('apps/user_ldap/api/v1/config/{configId}', {
    configId
  }), {
    configData: config
  });
  logger.debug('Updated configuration', {
    configId,
    config
  });
  return response.data.ocs.data;
}
export async function deleteConfig(configId) {
  try {
    await axios.delete(generateOcsUrl('apps/user_ldap/api/v1/config/{configId}', {
      configId
    }));
    logger.debug('Deleted configuration', {
      configId
    });
  } catch (error) {
    const errorResponse = error.response;
    showError(errorResponse?.data.ocs.meta.message || t('user_ldap', 'Fail to delete config'));
  }
  return true;
}
export async function testConfiguration(configId) {
  const params = new FormData();
  params.set('ldap_serverconfig_chooser', configId);
  const response = await axios.post(path.join(AJAX_ENDPOINT, 'testConfiguration.php'), params);
  logger.debug(`Configuration is ${response.data.status === 'success' ? 'valide' : 'invalide'}`, {
    configId,
    params,
    response
  });
  return response.data;
}
export async function clearMapping(subject) {
  const params = new FormData();
  params.set('ldap_clear_mapping', subject);
  const response = await axios.post(path.join(AJAX_ENDPOINT, 'clearMappings.php'), params);
  if (response.data.status === 'success') {
    logger.debug('Cleared mapping', {
      subject,
      params,
      response
    });
    showSuccess(t('user_ldap', 'Mapping cleared'));
  } else {
    showError(t('user_ldap', 'Failed to clear mapping'));
  }
}
export async function callWizard(action, configId) {
  let extraParams = arguments.length > 2 && arguments[2] !== undefined ? arguments[2] : {};
  const params = new FormData();
  params.set('action', action);
  params.set('ldap_serverconfig_chooser', configId);
  Object.entries(extraParams).forEach(_ref => {
    let [key, value] = _ref;
    params.set(key, value);
  });
  const response = await axios.post(path.join(AJAX_ENDPOINT, 'wizard.php'), params);
  if (response.data.status === 'error') {
    showError(response.data.message);
    throw new Error(response.data.message);
  }
  logger.debug(`Called wizard action: ${action}`, {
    configId,
    params,
    response
  });
  return response.data;
}
/**
 *
 * @param value
 */
export async function showEnableAutomaticFilterInfo() {
  return new Promise(resolve => {
    const dialog = getDialogBuilder(t('user_ldap', 'Mode switch')).setText(t('user_ldap', 'Switching the mode will enable automatic LDAP queries. Depending on your LDAP size they may take a while. Do you still want to switch the mode?')).addButton({
      label: t('user_ldap', 'No'),
      callback() {
        dialog.hide();
        resolve('1');
      }
    }).addButton({
      label: t('user_ldap', 'Yes'),
      callback() {
        resolve('0');
      }
    }).setSeverity(DialogSeverity.Info).build();
    dialog.show();
  });
}